import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Exame_funcao } from '../../../modelo/Exame_funcao'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'exame_funcao-dados-details',   templateUrl: 'exame_funcao_dadosDetails.component.html' }) export class Exame_funcaoDadosDetailsComponent implements OnInit {    exame_funcao: Exame_funcao;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.exame_funcao.Exame_funcao";                 let id = +params['id'];         this.exame_funcao = new Exame_funcao();         this.exame_funcao.cd_exame_funcao = id;         o.objetoJson = this.exame_funcao;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a exame_funcao. '; }      this.exame_funcao = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


